.notes-container {
  @apply max-w-[800px] mx-auto p-5;
}

.note-article {
  @apply mb-12 border-b border-gray-200;
}

.note-header h1 {
  @apply text-[1.8rem] mb-2 text-gray-800;
}

.note-meta {
  @apply text-gray-600 text-sm mb-4 flex justify-between items-center;
}

.note-markdown-content {
  @apply p-4 bg-gray-50 rounded-lg my-4;
}

textarea.note-editor {
  @apply w-full h-[20em] min-h-[20em] mt-2.5 p-2.5 border border-gray-300 rounded;
}

.action-primary-button {
  @apply bg-blue-600 text-white border-none py-2 px-4 rounded cursor-pointer transition-colors hover:bg-blue-700;
}

.action-secondary-button {
  @apply bg-gray-300 text-black border-none py-2 px-4 rounded cursor-pointer transition-colors hover:bg-gray-400;
}

.action-danger-button {
  @apply bg-red-500 text-white border-none py-2 px-4 rounded cursor-pointer transition-colors hover:bg-red-600;
}

.note-markdown-content h1 {
  @apply text-2xl font-bold my-4;
}

.note-markdown-content h2 {
  @apply text-xl font-bold my-3;
}

.note-markdown-content h3 {
  @apply text-lg font-bold my-2;
}

.note-markdown-content p {
  @apply my-2;
}

.note-markdown-content ul,
.note-markdown-content ol {
  @apply list-disc pl-5 my-2;
}

.note-markdown-content li {
  @apply my-1;
}

.note-markdown-content blockquote {
  @apply border-l-4 border-gray-300 pl-4 italic my-2;
}

.note-markdown-content code {
  @apply bg-gray-200 p-1 rounded;
}

.note-markdown-content pre {
  @apply bg-gray-200 p-2 rounded my-2;
}
